.App {
  background-color: black;
  overflow-x: hidden;
  overflow-y: hidden;
  height: 100vh;
  width: 100%;
  position: relative;
  z-index: 0;
}

.me{
  position: absolute;
  right:0;
  height: 100%;
  width: 60%;
  z-index: 2;
  opacity: 0; 
  overflow: hidden;
  animation-name: me;
  animation-duration: 3s;
  animation-delay: 1.5s; 
  animation-fill-mode: forwards;
}


.me img:hover{
  animation-name: colorHover;
  animation-duration: 1s;
  animation-delay: 0.2s;
  animation-fill-mode: forwards;
}

.me img {
  position: absolute;
  right:0;
  height: 100%;  
  filter: grayscale(100%);
  mask-image: linear-gradient(to right, transparent -15%, black 100%);
}

.names {
  color: lightgray;
  margin-top: -2rem;
  opacity: 0.6;
}

.names h1 {
  margin:0;
  margin-left: -5px;
  font-size: 8rem;
  font-weight: 700;
  text-shadow: 1px 1px 0 #0070b9, -1px 1px 0 #0070b9, -1px -1px 0 #0070b9, 1px -1px 0 #0070b9;
}

.names h4 {
  font-size: 4rem;
  font-weight: 400;
  margin: 0;
  text-shadow: 1px 1px 0 #0070b9, -1px 1px 0 #0070b9, -1px -1px 0 #0070b9, 1px -1px 0 #0070b9;
}

.bow {
  opacity: 0.2;
  margin-top: 2rem;
  width:90%;
  height: 70vh;
  background-image: url("./images/bow.png");
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
}

.main{
  padding-top: 4rem;
  height: 100%;
  width: 80%;
  color:white;
  margin:auto;
  z-index: -1;
  animation-name: intro;
  animation-duration: 5s;
  animation-fill-mode: forwards;
}

.menu{ 
  width: 80%;
  height: 3rem;
  color:white;
  position: absolute;
  left: 10%;
  bottom: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  background: linear-gradient(to left, transparent 0%, red 100%);
}

.menu div{
  width: 200px;
  font-size: 1.8rem;
  font-weight: 700;
  background: none; 
  z-index:2
}

.menu div:hover {
  cursor: pointer;
}

@keyframes intro {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


@keyframes me {
  0% {
    opacity: 0;
    height: 100vh;
    transform: translateX(200px);
  }
  99% {
    opacity: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 1;
    overflow-y: auto;
    transform: translateX(0);
  }
}

@keyframes colorHover {
  0% {
    filter: grayscale(100%);
  }
  100% {
    filter: grayscale(50%);
  }
}